
import { useTranslation } from "react-i18next";
import useDataFetch from "../../hook/useDataFetch";
import { MonthlyStats } from "../../interfaces/IsummData";
import classes from "./Summary.module.scss";
import SummaryBox from "./SummaryBox";
import { useState } from "react";
import { Button, Flex } from "@chakra-ui/react";


function Summary() {
  const { t } = useTranslation();
  const MonthlyStatsUrl = `${process.env.REACT_APP_API_URL}/api/portal/stats`;
  const [count, setCount] = useState(0);
  const { data, error, loading } = useDataFetch<MonthlyStats>(MonthlyStatsUrl);

  // Check if data exists before destructure
  const { monthly, allTime, currentYear } = data || {};

  const monthlyConnects = {
    icon: "arcticons:connect",
    text: count === 0 ?"thisMonthConnects":count === 0 ?"thisYearConnects":"thisTotalConnects",
    amount: count === 0 ? (monthly?.totalConnects || 0) : count === 1 ? (currentYear?.totalConnects || 0) : (allTime?.totalConnects || 0),
    currency: "",
  };

  const monthlySales = {
    // icon: "icon-park-outline:transaction-order",
    icon: "carbon:sales-ops",
    text: count === 0 ?"thisMonthRevenue":count === 1 ?"thisYearRevenue":"thisTotalRevenue",
    amount: count === 0 ? (monthly?.totalSales || 0) : count === 1 ? (currentYear?.totalSales || 0) : (allTime?.totalSales || 0),
    currency: "",
  };

  const monthlyConnectsCost = {
    // icon: "jam:coin",
    icon: "nimbus:money",
    // icon: "arcticons:connect",
    text: count === 0 ?"thisMonthUsedMoneyOnConnects":count === 1 ?"thisYearUsedMoneyOnConnects":"thisTotalUsedMoneyOnConnects",
    amount: count === 0 ? (monthly?.totalMoneyUsedOnConnects || 0) : count === 1 ? (currentYear?.totalMoneyUsedOnConnects || 0) : (allTime?.totalMoneyUsedOnConnects || 0),
    currency: "",
  };

  const monthlyProposals = {
    icon: "icon-park-outline:transaction-order",
    text: count === 0 ?"thisMonthProposals":count === 1 ?"thisYearProposals":'thisTotalProposals',
    amount: count === 0 ? (monthly?.totalProposals || 0) : count === 1 ? (currentYear?.totalProposals || 0) : (allTime?.totalProposals || 0),
    currency: "",
  };

  const monthlyAmount = {
    // icon: "akar-icons:shopping-bag",
    icon: "tdesign:money",
    text: count === 0 ?"thisMonthRecievedAmount":count === 1 ?"thisYearRecievedAmount" :"thisTotalRecievedAmount",
    amount: count === 0 ? (monthly?.totalReceivedAmount || 0) : count === 1 ? (currentYear?.totalReceivedAmount || 0) : (allTime?.totalReceivedAmount || 0),
    currency: "",
  };


  return (
    <section className={classes.summary}>
      <Flex justifyContent={'space-between'} alignItems={'center'}>
        <p className="subTitle">{t("summary")}</p>
        <Flex gap={'10px'}>  <Button colorScheme={count === 0 ? "red" : "gray"} onClick={() => setCount(0)}>Monthly</Button>
          <Button colorScheme={count === 1 ? "red" : "gray"} onClick={() => setCount(1)}>Yearly</Button>
          <Button colorScheme={count === 2 ? "red" : "gray"} onClick={() => setCount(2)}>Total</Button></Flex>
      </Flex>
      <div className={classes.summary__box}>
        {/* Render the SummaryBox only if data is available */}
        {data && (
          <>
          <SummaryBox key={monthlyProposals.text} item={monthlyProposals} />
          <SummaryBox key={monthlyConnects.text} item={monthlyConnects} />
            <SummaryBox key={monthlySales.text} item={monthlySales} />
            <SummaryBox key={monthlyConnectsCost.text} item={monthlyConnectsCost} />
            <SummaryBox key={monthlyAmount.text} item={monthlyAmount} /></>
        )}
      </div>
    </section>
  );
}


export default Summary;
